<template>
    <transition name="modal" appear>
        <div class="modal modal-mask" id="modal_auth">
            <div class="modal-content" id="card_auth">
                <div class="card light">
                    <div class="card-header">
                        <slot name="header">
                            <div class="card-title">확인해 보세요</div>
                        </slot>
                        <button type="button" class="close-modal" @click="close"></button>
                    </div>
                    <div class="card-body">
                        <div class="container-fluid">
                            <slot name="body"></slot>
                            <!--
                            <p class="guide-text">{{ guideText }}</p>
                            <form action="" v-on:submit.prevent="submitForm">
                                <div>
                                    <input type="password" id="modal_pw" v-model="passwd"/>
                                </div>
                                <button type="submit" class="block primary">확인</button>
                            </form>-->
                        </div>
                    </div>
                    <div class="card-footer">
                        <slot name="footer">
                            <button type="button" class="block primary">확인</button>
                        </slot>
                    </div>
                </div>
            </div>
        </div>
    </transition>

</template>
<script>
    export default {
        name:"CardModal",
        methods: {
            close() {
                this.$emit('close');
            },
        },
    }
</script>

<style>
.modal-content { max-width:600px; }
</style>