<template>
    <div class="page-content">
        <!-- 추후 form component file로 분할-->
        <div class="form-wrapper">
            <form action="" @submit.prevent="onSubmitQuery">
                <div class="row">
                    <div class="form-group">
                        <label for="DateFrom">From</label>
                        <input type="date" id="date_from" placeholder="시작일" v-model="fromDate"/>
                    </div>
                    <div class="form-group">
                        <label for="DateTo">To</label>
                        <input type="date" id="data_to" v-model="toDate"/>
                    </div>
                <button type="submit">Search</button>
                </div>
            </form>
        </div>
        <!-- <result-tras :results='res' @openDet='openDetail'></result-tras> -->
        <div id="loader_1"></div>
        <result-tras v-show="showResults"></result-tras>
    </div>
</template>
<script>
// 임시 데이터 json load
//import res from '@/assets/json/transactions.json'
//import axios from 'axios'

import api from '@/utils/api'
import ResultTras from './ResultTras.vue'

export default {
    name:'SearchTras',
    components: {
        ResultTras,
    },
    data() {
        return {
            fromDate:'',
            toDate:'',
            //res:''
            showResults:false,
        }
    },
    methods:{
        //lazyLoadJson:()=>import('@/assets/json/transaction_modified.json'),
        maskName: name=>{
            if (name.length <= 2) {
                return name.replace(name.substring(0, 1), "*");
            }

            return (
                name[0] +
                "*".repeat(name.substring(1, name.length - 1).length) +
                name[name.length - 1]
            );
        },
        // submit button(Search)
        onSubmitQuery:function(){
            // axios로 데이터 로딩
            /*axios.get('transactions.json')
                .then(res=>{
                    console.log(res.data);
                    this.$store.dispatch('loadTransactions',res.data);
                })
                .catch(err=>{
                    alert('파일을 읽지 못했습니다');
                    console.log(err);
                })
            */
           // api.tras
           // show loader in loading data
            document.getElementById('loader_1').style.display = 'block';

            api.tras(this.fromDate, this.toDate, '', 'approve')
            .then(res=>{
                console.log(res.data);
                if(res.data.result!="denial") {
                    res.data.tras.forEach(tra=>{
                        tra.uname_m = this.maskName(tra.uname);
                    });
                    //this.res = data;
                    this.$store.dispatch('loadTransactions',res.data);
                }
                this.showResults = true
                document.getElementById('loader_1').style.display = 'none';
            }).catch(error=>{
                console.log(error.message);
                document.getElementById('loader_1').style.display = 'none';
            })
           

            // lazy loading 임시 데이터 json file - Promise를 기본으로 return해 주네?
            /*this.lazyLoadJson()
            .then(data=>{
                console.log(data);
                data.tras.forEach(tra=>{
                    tra.uname = this.maskName(tra.uname);
                });
                //this.res = data;
                this.$store.dispatch('loadTransactions',data);
            })
            .catch(err=>{
                console.log(err);
                alert('파일 읽기에 실패하였습니다');
            })*/
        },
        /*openDetail(item) {
            this.$emit('openDet',item);
        }*/
    },
    mounted(){ 
        //this.$emit('viewTras',false); //console.log(this.res.count, this.res.tras)
        // 내역조회 UI가 열릴 때는 뷰모드를 search로
        this.$emit('viewMode','search');
    }
}
</script>
<style scoped>
.form-group { min-width:300px; }
.form-group label { margin-bottom:5px }

#loader_1 { display:none; margin:100px 0; position:relative; left:50%; top:50%; transform:translateX(-50%); border:6px solid var(--bg-white); border-top-color:var(--primary); border-radius:50%; width:60px; height:60px; -webkit-animation:spin 2s linear infinite; animation:spin 2s linear infinite; }
/* Safari */
@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); } 
}
</style>